import { Component, OnInit, AfterViewInit, OnDestroy, Inject, Input } from '@angular/core';
import { AddCompanyDialogComponent } from './add-company-dialog/add-company-dialog.component';
import { MatDialog } from '@angular/material';
import { MalihuScrollbarService } from 'ngx-malihu-scrollbar';
import { EditCompaniesDialogComponent } from './edit-companies-dialog/edit-companies-dialog.component';
import { RemoveCompaniesDialogComponent } from './remove-companies-dialog/remove-companies-dialog.component';
import { GetCompanyListService } from './_services/get-company-list.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { AuthService } from '../_service/auth.service';


@Component({
  selector: 'app-companies',
  templateUrl: './companies.component.html',
  styleUrls: ['./companies.component.scss']
})
export class CompaniesComponent implements OnInit {
  serverError: string;
  companyDetails;

  // public scrollbarOptions = {
  //   axis: 'y', theme: 'minimal-dark', setHeight: '417px'
  // };
  successMessageCompany;
  errorMessageCompany;


  constructor(public dialog: MatDialog, private mScrollbarService: MalihuScrollbarService, private getCompanyListService: GetCompanyListService, private spinnerService: Ng4LoadingSpinnerService, private authService: AuthService, ) { }

  // ngAfterViewInit() {
  //   this.mScrollbarService.initScrollbar('#dialog-scroll', { axis: 'y', theme: 'dark-3', scrollButtons: { enable: true }, });
  // }

  // ngOnDestroy() {
  //   this.mScrollbarService.destroy('#dialog-scroll');
  // }
  ngOnInit() {
    this.getCompanyList();
  }
  openDialog(): void {
    const dialogRef = this.dialog.open(AddCompanyDialogComponent, {
      width: '770px',
      height: '430px',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.getCompanyList();
      }
    });


  }
  openDialogEditCompany(companyName, companyCode, countryName): void {
    const dialogRef = this.dialog.open(EditCompaniesDialogComponent, {
      width: '770px',
      height: '430px',
      disableClose: true,
      data: { companyName: companyName, companyCode: companyCode , countryName: countryName}
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.getCompanyList();
      }
    });
  }
  openDialogRemoveCompany(): void {
    const dialogRef = this.dialog.open(RemoveCompaniesDialogComponent, {
      disableClose: true,
    });
  }

  getCompanyList() {
    const companyDetailsInput = {
      'email': this.authService.email,
      'role': this.authService.userRole,
      'moduleId': 'CP',
      'screenId': '12',
      'sessionId': this.authService.sessionId,
      'accessToken': this.authService.accessToken,
      'clientType': 'web'
    };
    this.spinnerService.show();
    this.getCompanyListService.getCompanyListService(companyDetailsInput).subscribe(
      (res) => {
        this.companyDetails = res.body.companyList;

        if (res['status'] === 200) {

          if (this.companyDetails.status !== 'FAILED') {

            this.successMessageCompany = res.body.message;
            this.errorMessageCompany = '';
          } else {
            this.errorMessageCompany = res.body.message;
          }
        } else {
          this.serverError = res.error;
        }
        this.spinnerService.hide();
      });
  }
}
