import { Component, OnInit, Inject, ɵConsole, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Input, Output, EventEmitter } from '@angular/core';
import { HttpHeaders, HttpEventType, HttpClient, HttpRequest, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { GetAddCompanyService } from '../_services/get-add-company.service';
import { AuthService } from '../../_service/auth.service';
import { MAT_DIALOG_DATA, MatDialogRef, MatAutocompleteSelectedEvent, MatAutocompleteTrigger, MatSort } from '@angular/material';
import { GetCountryNameService } from '../_services/get-country-name.service';
import { debounceTime, distinctUntilChanged, startWith, map } from 'rxjs/operators';

@Component({
  selector: 'app-add-company-dialog',
  templateUrl: './add-company-dialog.component.html',
  styleUrls: ['./add-company-dialog.component.scss']
})
export class AddCompanyDialogComponent implements OnInit {
  addCompaniesForm: FormGroup;
  serverError: string;
  fileName: any;
  successMessageCompany;
  errorMessageCompany;
  showAlreadyExistError;
  fileToUpload: File = null;
  maxFileError = false;
  fileFormatError = false;
  countries: any[];
  filteredOptions: Observable<any>;
  searchedDialogContent: any[];
  autoComplete: MatAutocompleteTrigger;
  @ViewChild('autoCompleteInput', { read: MatAutocompleteTrigger })
  @ViewChild(MatSort) sort: MatSort;
  wrongCountryError = false;
  constructor(private formBuilder: FormBuilder, private http: HttpClient, private getAddCompanyService: GetAddCompanyService, private spinnerService: Ng4LoadingSpinnerService, private authService: AuthService, public dialogRef: MatDialogRef<AddCompanyDialogComponent>, private getCountryNameService: GetCountryNameService) {
    this.addCompaniesForm = this.formBuilder.group({
      companyName: ['', [Validators.required]],
      // companyCode: ['',  [Validators.required, Validators.minLength(4)]],
      companyLogo: [''],
      countryName: ['', [Validators.required]]
    });
  }

  validationMsg = {
    // companyCode: {
    //   required: 'Company Code cannot be left blank',
    //   minlength: 'Company Code should be of 4 characters'
    // },
    companyName: {
      required: 'Company Name cannot be left blank',
    },
    countryName: {
      required: 'Country Name cannot be left blank',
    }
  };

  getErrorMessage(formControlName: string) {
    const formControl = this.addCompaniesForm.get(formControlName);
    let message = '';

    if (formControl.errors) {
      Object.keys(formControl.errors).forEach(key => {
        message = this.validationMsg[formControlName][key];
      });
    }

    return message;
  }

  ngOnInit() {
    this.getCountryNameService.getCountryCode()
      .subscribe(
        data => {
          this.countries = data as any[];
          this.countries.sort(function (a, b) { // sort country code
            // return a.country_calling_code - b.country_calling_code;
            return compare(a.country_name, b.country_name, true);
          });
          this.filteredOptions = this.addCompaniesForm.controls.countryName.valueChanges.pipe(
            debounceTime(500),
            distinctUntilChanged(),
            startWith(''),
            map(value => this._filter(value)),
          );
        },
        (err: HttpErrorResponse) => {
        }
      );
  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();
    return this.countries
      .filter(option => option.country_name.toLowerCase().indexOf(filterValue) > -1);
  }

  onChange(files: FileList) {
    if (files.item(0).size > 20950) {
      this.maxFileError = true;
      this.fileFormatError = false;
      this.addCompaniesForm.get('companyLogo').reset();
    } else if (files.item(0).type !== 'image/png' && files.item(0).type !== 'image/jpeg') {
      this.fileFormatError = true;
      this.maxFileError = false;
      this.addCompaniesForm.get('companyLogo').reset();
    } else {
      this.fileToUpload = files.item(0);
      this.fileFormatError = false;
      this.maxFileError = false;
      this.fileName = this.fileToUpload.name;
    }
  }

  removeSelectedTemplate() {
    this.fileName = false;
    this.addCompaniesForm.get('companyLogo').reset();
    this.fileToUpload = null;
  }

  saveAddCompanyForm() {
    const countryName = this.addCompaniesForm.controls.countryName.value;
    const existingCountries = this.countries.map((obj) => obj.country_name);
    if (existingCountries.includes(countryName)) {
      this.wrongCountryError = false;
    } else {
      this.wrongCountryError = true;
    }
    if (this.wrongCountryError === false) {
      this.getAddCompany();
    }
  }

  getAddCompany() {
    const addCompaniesForm = this.addCompaniesForm;
    const countryName = this.addCompaniesForm.controls['countryName'].value;
    // console.log(countryName);

    let fileToSend;
    if (this.fileToUpload !== null) {
      fileToSend = this.fileToUpload;
    }

    const addCompanyDetailsInput = {
      'email': this.authService.email,
      'role': this.authService.userRole,
      'moduleId': 'CP',
      'screenId': '12',
      'sessionId': this.authService.sessionId,
      'accessToken': this.authService.accessToken,
      // '_id': this.addCompaniesForm.controls['companyCode'].value,
      'companyName': this.addCompaniesForm.controls['companyName'].value,
      'logoFile': fileToSend,
      'action': 'addCompany',
      'clientType': 'web',
      'country': countryName
    };
    this.spinnerService.show();
    this.getAddCompanyService.getAddCompanyService(addCompanyDetailsInput).subscribe(
      (res) => {
        if (res['status'] === 200) {
          const addCompanyDetails = res.body;
          if (addCompanyDetails.status !== 'FAILED') {
            this.successMessageCompany = res.body.message;
            this.errorMessageCompany = '';
            this.dialogRef.close(addCompanyDetails);
            this.showAlreadyExistError = false;
          }
        } else {
          this.showAlreadyExistError = true;
          this.errorMessageCompany = res.error.message;
        }
        this.spinnerService.hide();
      });
  }

  selected(event: MatAutocompleteSelectedEvent): void {
    this.searchedDialogContent = [];
    this.searchedDialogContent.push({
      country_name: event.option.viewValue
    });
    // if (event.option.viewValue === '') {
    //   this.ContactInfoState = true;
    // }
  }

  handleEmptyInput(event: any) {
    if (event.target.value === '') {
      this.wrongCountryError = false;
    }
  }
}

function compare(a: string, b: string, isAsc: boolean) {
  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}
