import { Injectable } from '@angular/core';
import { of, Observable } from 'rxjs';
import { catchError, tap, map } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { environment } from '../../../environments/environment';
import { HandleErrorService } from 'src/app/_service/handle-error.service';

@Injectable({
  providedIn: 'root'
})
export class GetAddCompanyService {
  getAddCompanyServiceUrl = environment.contentServer + '/api/contentMgmt/addCompany';
  httpOptions = {
    headers: new HttpHeaders({}),
    observe: 'response' as 'body'
  };
  constructor(private httpClient: HttpClient, private handleErrorService: HandleErrorService) { }

  getAddCompanyService(input): Observable<any> {

    const formData = new FormData();

    Object.keys(input).forEach(key => {
      formData.append(key, input[key]);
    });

    return this.httpClient.post<any>(this.getAddCompanyServiceUrl, formData, this.httpOptions)
      .pipe(
        tap(res => {
          console.log(res);
        }),
        catchError(this.handleErrorService.handleError('getAddCompanyService', []))
      );
  }
}
